/* eslint-disable react/prop-types */
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import {
 AppBar, Toolbar, Button, Fab,
} from '@material-ui/core';
import { GetAppRounded, Refresh } from '@material-ui/icons';
import { styles } from './styles/Navbar';
import NavbarLink from './NavbarLink';

const Navbar = ({ location, classes }) => {
    const currentPacht = location.pathname;
    let backLink;
    if (currentPacht === '/about' || currentPacht === '/bookmarks' || currentPacht === '/what') {
        backLink = <NavbarLink path="/" title="بازگشت" />;
    } else {
        backLink = '';
    }
    return (
        <>

            <AppBar position="fixed" color="primary" className={classes.appBar}>
                <Toolbar>
                    <NavbarLink path="/about" title="درباره ما" />
                    <NavbarLink path="/what" title="ایونتا چیه؟" />
                    <NavbarLink path="/bookmark" title="بوکمارک های من" />
                    {backLink}
                    <Fab color="secondary" aria-label="add" className={classes.fabButton}>
                        <Refresh />
                    </Fab>
                    <div className={classes.grow} />

                    <Button startIcon={<GetAppRounded />} className={`${classes.buttonApp} add-button`} variant="contained" color="default">
                        نصب اپلیکیشن
                    </Button>
                </Toolbar>
            </AppBar>
        </>
    );
};
export default withRouter(withStyles(styles)(Navbar));
