import React from 'react';

import {
    CssBaseline, Button, Typography, Paper, Box,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Link, withRouter } from 'react-router-dom';

// eslint-disable-next-line arrow-body-style
const useStyles = makeStyles((theme) => ({
    text: {
        padding: theme.spacing(2, 2, 0),
        fontSize: 25,
        textAlign: 'center',
    },
    paper: {
        paddingBottom: 20,
    },
    discreption: {
        textAlign: 'center',
        [theme.breakpoints.down('md')]: {
            fontSize: 13,
        },
    },
}));

const Header = ({ location }) => {
    const classes = useStyles();
    const currentPacht = location.pathname;

    return (
        <>
            <CssBaseline />
            <Paper square className={classes.paper}>
                <Typography className={classes.text} variant="h1" gutterBottom>
                    دریافت رویداد روزانه
                </Typography>

                <Typography className={classes.discreption} component="p">
                    دریافت رویداد های روزانه‌ی تقویم های هجری و شمسی و اطلاع از تعطیلی آن
                </Typography>
                <Box mt={2} display="flex" justifyContent="center">
                    {currentPacht === '/convert' ? <Link to="/"><Button variant="contained" color="primary">رویدادها</Button></Link> : <Link to="/convert"><Button variant="contained" color="primary">تبدیل تاریخ</Button></Link>}
                </Box>
            </Paper>
        </>
    );
};
export default withRouter(Header);
